import express from 'express';
const router = express.Router();

import fetchAccount from './ctrls/fetchAccount'
import checkNewAgentUsername from './ctrls/checkNewAgentUsername'
import createNewAgent from './ctrls/createNewAgent'
import fetchAgentStatus from './ctrls/fetchAgentStatus'
import addCredit from './ctrls/addCredit'
import fetchOpenBet from './ctrls/fetchOpenBet'
import fetchHistoryBet from './ctrls/fetchHistoryBet'
import addGame from './ctrls/addGame'


import { fullUpdateDB_MW, fetchAllEventOdds_MW, buildSportLeagueTable_MW, fullResultUpdateDB_MW } from '../../middleware/updateDB'

router.route('/fetch-account').get(fetchAccount)
router.route('/check-new-agent-username').post(checkNewAgentUsername)
router.route('/create-new-agent').post(createNewAgent)
router.route('/fetch-agent-status').post(fetchAgentStatus)
router.route('/add-credit').post(addCredit)
router.route('/fetch-open-bet').get(fullResultUpdateDB_MW, fetchOpenBet)
router.route('/fetch-history-bet').get(fullResultUpdateDB_MW, fetchHistoryBet)
router.route('/add-game').post(addGame)

export default router